body{
    direction: ltr;
    font-family: 'MontserratRegular', sans-serif;
}
*{
    font-family: inherit;
}
.main-header nav .main-nav .search-box{
    margin-right: 0;
    margin-left: auto;
}
.main-header .search-overlay button {
    top: 50%;
    left: unset;
    right: 10px;
}


.main-header nav .main-nav .collaps span:nth-child(2){
    margin-right: 0;
    margin-left: auto;

}
.main-header nav .main-nav .lang a{
    font-family: "GESS";
}
.main-header .nav-overlay .collaps,
.main-header .search-overlay .collaps{
    top: 10%;
    left: unset;
    right: 5%;

}
.main-header .nav-overlay .links{
    margin-right: 0;
    margin-left: 226px;
    @media (max-width: 425px){
        margin-left: 0;

    }
}
.main-header .nav-overlay .links .main-li::before{
    right: unset;
    top: 50%;
    left: -100px;
}
.main-header .nav-overlay .links .main-li .sup-link{
    margin-right: 0;
    margin-left: 67px;
    @media (max-width: 991px){
        margin: 5px 0;

    }
}

.main-header .nav-overlay .links .main-li .sup-link li{
    margin-right: 0;
    margin-left: 30px;
}
.main-header .overlay .titel h1,
.faci h2,
.why h2,
.services h2,
.about-section .about-content h2,
.client .client-content h2{
    font-family: "MontserratBold";

}

.main-header .overlay .titel p,
.faci p,
.why .why-containr .why-box p,
.client .client-content p,
.about-section .about-content p{
    font-family: "MontserratRegular";

}
.about-section .about-content p{
    padding-left: 0;

}
.about-section .about-icon .content{
    margin-right: 0;
    margin-left: 37px;
    @media (max-width: 991px){
        margin-left: 0;
        
    }

}
.services .services-containr .services-icon .content {
    margin-right: 0;
    margin-left: 30px;
    @media (max-width: 991px){
        margin-left: 0;
        
    }
}
.why .why-containr .why-box:not(:first-child) {
    margin-right: 0;
    margin-left: 30px;
}
.main-footer .right{
    border-left: none;
    border-right: 1px solid #2b2b2b;
    @media (max-width: 425px){
    border-right: none;

    }
}
.main-footer .left{
    border-right: none;
    border-left: 1px solid #2b2b2b;
    @media (max-width: 425px){
        border-left: none;
    }
    
}
.main-footer .center .social a:not(:first-child){
    margin-right: 0;
    margin-left: 6px;
}
.main-footer .center .copy img {
    margin-right: 0;
    margin-left: 3px;
}

.faci {
    overflow: hidden;
}
.faci .faci-content {
    @media (max-width:991px) {
        width: 100%;
        
    }
}

